import React from 'react'
import "../AboutHeader/AboutHeader.scss"
import { Link } from "react-router-dom"
import { AboutHeaderText } from "../../data/Data"


const AboutHeader = () => {
  return (
    <div>
        <section className='header'>
        <div className='scontainer'>
          <div className='content grid3'>
            {AboutHeaderText.map((val, index) => (
              <div className='header__about' key={index}>
                <div className='text'>
                 <h5 className='about__h5'>{val.h5}</h5>
                 <h1 className='about__h1'>{val.h1}</h1>
                <p className='about__p'>{val.p}</p>
                 <Link to='/'>
                  <i className='i fa fa-long-arrow-alt-right'></i> To'liq ma'lumot
                </Link>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  )
}

export default AboutHeader