import React from 'react'
import "../AboutSlider/AboutSlider.scss"
import { SliderImages } from "../../data/Data"
import Slider from "react-slick";

// Import css files
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const AboutSlider = () => {

    const settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        initialSlide: 0,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 3,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      };

  return (
    <div>
         <section className='section'>
          <div className='content grid3'>
            <div className="jcontainer">
          <Slider className='slider' {...settings}>
            {SliderImages.map((val, index) => (
              <div className='section__slider' key={index}>
                    <div className='slider__card'>
                         <img className='jjjj'  src={val.image} alt="" />
                            <h1><img className='logo__slider' src="https://static1.squarespace.com/static/4ff37448e4b03ec22b10dd14/t/5e0a5cab7fbbdb349c97c163/1666139102984/"alt="" /></h1>
                    </div>
              </div>
            ))}
           </Slider>
           </div>
          </div>
      </section>
        
    </div>
  )
}

export default AboutSlider